import React, { useState } from "react";
import { Form, Input, Button, DatePicker, InputNumber } from "antd";
import { useFormik } from "formik";
import moment from "moment";
import adminMovieApi from "apis/adminMovieApi";
import { GROUP_ID } from "settings/apiConfig";
import { useHistory } from "react-router";
import * as yup from "yup";
import "./Movie.scss";
export default function AddNewMovie() {
  const [imgSrc, setImgSrc] = useState("");
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      tenPhim: "",
      trailer: "",
      moTa: "",
      maNhom: GROUP_ID,
      ngayKhoiChieu: "",
      danhGia: "",
      hinhAnh: {},
    },
    validationSchema: yup.object({
      tenPhim: yup
        .string()
        .matches(
          /^[a-zA-Z ,.'-]+$/
        )
        .required(),
      trailer: yup.string().required(),
      moTa: yup.string().required(),
      ngayKhoiChieu: yup
        .date()
        .default(() => {
          return new Date();
        })
        .required(),
      maNhom: yup.string(),
      hinhAnh: yup.string().required("Bn cha chn hnh nh !"),
      danhGia: yup
        .string()

        .matches(/^[0-9]+$/)
        .required(),
    }),
    onSubmit: (values) => {
      //to formdata=> a gi tr values t formik vo formData
      let formData = new FormData();
      //x l hnh nh, nu cha c hnh nh s thm trng hnh nh vo form,ta s thm hnh nh bng File
      for (let key in values) {
        if (key !== "hinhAnh") {
          formData.append(key, values[key]);
        } else {
          formData.append("File", values.hinhAnh, values.hinhAnh.name);
        }
      }
      //goi api post len server
      adminMovieApi
        .addMovie(formData)
        .then((res) => {
          // console.log(res.data);
        })
        .catch((err) => console.log(err));
    },
  });
  const handleChangeDatapicker = (value) => {
    let ngayKhoiChieu = moment(value).format("DD/MM/YYYY");
    formik.setFieldValue("ngayKhoiChieu", ngayKhoiChieu);
  };

  const handleChangeFile = (e) => {
    // ly file t event
    const file = e.target.files[0];
    // to i tng  c file hin th nh
    const readFile = new FileReader();
    readFile.readAsDataURL(file);
    readFile.onload = (e) => {
      setImgSrc(e.target.result);
    };
    formik.setFieldValue("hinhAnh", file);
  };
  // to bin ring  trnh s dng li nhiu nh hng performance
  const errors = formik.errors;
  const touched = formik.touched;
  const values = formik.values;
  return (
    <>
      <h3>THM PHIM</h3>
      <Form
        onSubmitCapture={formik.handleSubmit}
        className="text text-left"
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
      >
        <Form.Item label="Tn phim">
          <Input name="tenPhim" 
          placeholder="Nhp tn phim" onChange={formik.handleChange} />
          {errors.tenPhim &&
            touched.tenPhim &&
            (values.tenPhim === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Trailer">
          <Input name="trailer" placeholder="Nhp trailer phim" onChange={formik.handleChange} />
          {errors.trailer && touched.trailer && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>

        <Form.Item label="M t">
          <Input name="moTa" placeholder="Nhp m t" onChange={formik.handleChange} />
          {errors.moTa && touched.moTa && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>

        <Form.Item label="Ngy khi chiu">
          <DatePicker

            name="ngayKhoiChieu"
            format={"DD-MM-YYYY"}
            onChange={handleChangeDatapicker}
            placeholder="Ngy khi chiu"
          />
          {errors.ngayKhoiChieu && touched.ngayKhoiChieu && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>
        <Form.Item  label="nh gi">
          <Input style={{ width: "100px" }} name="danhGia"
          placeholder="nh gi" onChange={formik.handleChange} />
          {errors.danhGia &&
            touched.danhGia &&
            (values.danhGia === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Ch c nhp s (1-10) !</p>
              </div>
            ))}
        </Form.Item>

        <Form.Item label="Hnh nh">
          <Input
            type="file"
            name="hinhAnh"
            onChange={handleChangeFile}
            accept="image/jpg,image/jpeg,image/gif,image/png"
            placeholder="Chn hnh nh"
          />
          {errors.hinhAnh && touched.hinhAnh && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
          <br />
          <img
            src={imgSrc}
            style={{ width: "150px", height: "150px" }}
            alt="..."
          />
        </Form.Item>
        <Form.Item className="text text-center btnAddMovie">
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button "
          >
            THM
          </Button>
          <Button
            type="primary"
            className="ml-5"
            onClick={() => {
              history.push("/admin/movie");
            }}
          >
            QUAY LI
          </Button>
        </Form.Item>
      </Form>
    </>
  );
}
