import React, { useEffect, useState } from "react";
import { Form, Input, Button, Select } from "antd";
import { useFormik } from "formik";
import { GROUP_ID } from "settings/apiConfig";
import userApi from "apis/userApi";
import { useSelector } from "react-redux";
import { useHistory, useParams } from "react-router";
import { NavLink } from "react-router-dom";
import * as yup from "yup";
import "./User.scss";
//CONTENT
export default function EditUser() {
  let [inforDetailUser, setInforDetailUser] = useState("");

  const history = useHistory();
  const { currentUser } = useSelector((state) => state.authUserReducer);
  const params = useParams();
  useEffect(() => {
    userApi
      .getInforDetailUser(params.taiKhoan)
      .then((res) => {
        // v thng tin ti khon api tr v l 1 mg vi duy nht 1 obj nn phi dn [0] ly obj u tin l thong itn ngi dng
        setInforDetailUser((inforDetailUser = res.data[0]));
      })
      .catch((err) => {
        console.log(err.response?.data);
      });
  }, []);
  //to form  cha thng tin ngi dng  cp nht
  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      taiKhoan: inforDetailUser.taiKhoan,
      matKhau: inforDetailUser.matKhau,
      email: inforDetailUser.email,
      soDt: inforDetailUser.soDt,
      maNhom: GROUP_ID,
      maLoaiNguoiDung: inforDetailUser.maLoaiNguoiDung,
      hoTen: inforDetailUser.hoTen,
    },
    validationSchema: yup.object({
      taiKhoan: yup.string().matches(/^\w+$/).required(),
      matKhau: yup
        .string()
        .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{6,10}$/)
        .required(),
      email: yup.string().email().required("Email khng ng nh dng"),
      soDt: yup
        .string()
        .matches(/^[0-9]*$/)
        .max(10)
        .required("S in thoi cha ng nh dng !"),
      maNhom: yup.string(),
      maLoaiNguoiDung: yup.string().required("Bn cha chn loi ngi dng !"),
      hoTen: yup
        .string()
        .matches(
          /^[a-zA-Z ,.'-]+$/
        )
        .required(),
    }),
    onSubmit: (values) => {
      console.log("values",values)
      userApi
        .editUser(values, currentUser.accessToken)
        .then((res) => {
          alert("Cp nht thnh cng !");
          history.push("/admin/user");
        })
        .catch((err) => {
          console.log(err.response?.data);
        });
    },
  });
  const handleChangeLoaiNguoiDung = (value) => {
    console.log(value);
    formik.setFieldValue("maLoaiNguoiDung", value);
  };
  // to bin ring  trnh s dng li nhiu nh hng performance
  const errors = formik.errors;
  const touched = formik.touched;
  const values = formik.values;
  //responsive
  
  return (
    <>
      <h2>Cp nht thng tin</h2>
      <Form
      className="text-left"
        onSubmitCapture={formik.handleSubmit}
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 10,
        }}
      >
        <Form.Item label="Ti khon">
          <Input
            disabled
            name="taiKhoan"
            onChange={formik.handleChange}
            value={formik.values.taiKhoan}
          />
          {errors.taiKhoan &&
            touched.taiKhoan &&
            (values.taiKhoan === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Mt khu">
          <Input
            name="matKhau"
            onChange={formik.handleChange}
            value={formik.values.matKhau}
          />
          {errors.matKhau &&
            touched.matKhau &&
            (values.matKhau.length < 6 || values.matKhau.length > 10 ? (
              <div className="styleErrors">
                <p>_  di k t t 6 n 10</p>
                <p>_ Khng s dng k t c bit</p>
                <p>_ t nht 1 k t thng , hoa v ch s</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng k t c bit</p>
                <p>_ t nht 1 k t thng , hoa v ch s</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="H tn">
          <Input
            name="hoTen"
            onChange={formik.handleChange}
            value={formik.values.hoTen}
          />
          {errors.hoTen &&
            touched.hoTen &&
            (values.hoTen === "" ? (
              <div  className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Khng cha cc k t c bit v du cu</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng cha cc k t c bit v du cu</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="S in thoi">
          <Input
            name="soDt"
            onChange={formik.handleChange}
            value={formik.values.soDt}
          />
          {errors.soDt &&
            touched.soDt &&
            (values.soDt === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ S in thoi khng ng nh dng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ S in thoi khng ng nh dng</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Email">
          <Input
            name="email"
            onChange={formik.handleChange}
            value={formik.values.email}
          />{" "}
          {errors.email &&
            touched.email &&
            (values.email === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Email khng ng nh dng(ex: abc@gmail.com)</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Email khng ng nh dng(ex: abc@gmail.com)</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Loi ngi dng">
          <Select
            name="maLoaiNguoiDung"
            options={[
              { label: "Qun Tr", value: "QuanTri" },
              { label: "Khch hng", value: "KhachHang" },
            ]}
            onChange={handleChangeLoaiNguoiDung}
            placeholder="Chn loi ngi dng"
            value={formik.values.maLoaiNguoiDung}
          />
        </Form.Item>

        <Form.Item className="btnEditUser" style={{ marginLeft: "307px" }}>
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button btn1"
          >
            Cp nht
          </Button>
          <NavLink to="/admin/user">
            <Button type="primary" className="login-form-button btn2 ml-5">
              Quay li
            </Button>
          </NavLink>
        </Form.Item>
      </Form>
    </>
  );
}
