import React, { useState, useEffect } from "react";
import { Form, Input, Button, DatePicker, InputNumber } from "antd";
import { useFormik } from "formik";
import moment from "moment";
import { GROUP_ID } from "settings/apiConfig";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router";
import { actEditMovie, actGetInforMovieDetailEdit } from "./module/action";
import * as yup from "yup";
//CONTENT
export default function EditMovie() {
  const [imgSrc, setImgSrc] = useState("");
  const dispatch = useDispatch();
  const history = useHistory();
  const { editMovieDetail } = useSelector((state) => state.movieEditReducer);
  const { currentUser } = useSelector((state) => state.authUserReducer);
  const params = useParams();
  // ly thng tin chi tit 1 b phim  thay i
  useEffect(() => {
    dispatch(actGetInforMovieDetailEdit(params.maPhim));
  }, []);
  //to form  ly li gi tr cc trng input nh vo api ly thng tin phim qua m phim
  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      maPhim: editMovieDetail?.maPhim,
      tenPhim: editMovieDetail?.tenPhim,
      trailer: editMovieDetail?.trailer,
      moTa: editMovieDetail?.moTa,
      maNhom: GROUP_ID,
      ngayKhoiChieu: editMovieDetail?.ngayKhoiChieu,
      danhGia: editMovieDetail?.danhGia,
      hinhAnh: null,
    },
    validationSchema: yup.object({
      tenPhim: yup
        .string()
        .matches(
          /^[a-zA-Z ,.'-]+$/
        )
        .required(),
      trailer: yup.string().required(),
      moTa: yup.string().required(),
      ngayKhoiChieu: yup
        .date()
        .default(() => {
          return new Date();
        })
        .required(),
      maNhom: yup.string(),
      hinhAnh: yup.string().required("Bn cha chn hnh nh !"),
      danhGia: yup
        .string()

        .matches(/^[0-9]+$/)
        .required(),
    }),
    onSubmit: (values) => {
      //to formdata=> a gi tr values t formik vo formData
      let formData = new FormData();

      for (let key in values) {
        if (key !== "hinhAnh") {
          formData.append(key, values[key]);
        } else {
          if (values.hinhAnh !== null) {
            formData.append("File", values.hinhAnh, values.hinhAnh.name);
          }
        }
      }
      //goi api post edit len server(a history vo  action nhn c v khi thay i thanh cng s redirect trc tip qua trang phim v cp nht li mng phim)
      dispatch(actEditMovie(formData, currentUser.accessToken, history));
    },
  });
  const handleChangeDatapicker = (value) => {
    let ngayKhoiChieu = moment(value);
    formik.setFieldValue("ngayKhoiChieu", ngayKhoiChieu);
  };

  
  const handleChangeFile = (e) => {
    // ly file t event
    let file = e.target.files[0];
    // to i tng  c file hin th nh
    if (file) {

      let readFile = new FileReader();
      readFile.readAsDataURL(file);
      // setImgSrc(readFile.result);
      readFile.onload = function (e) {
        setImgSrc(e.target.result);
      };
      formik.setFieldValue("hinhAnh", file);

    }
  };
// to bin ring  trnh s dng li nhiu nh hng performance
const errors = formik.errors;
const touched = formik.touched;
const values = formik.values;
  return (
    <>
      <h3>CP NHT PHIM</h3>
      <Form
        onSubmitCapture={formik.handleSubmit}
        className="text text-left"
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
      >
        <Form.Item label="Tn phim">
          <Input
          placeholder="Nhp tn phim"
            name="tenPhim"
            onChange={formik.handleChange}
            value={formik.values.tenPhim}
          />
          {errors.tenPhim &&
            touched.tenPhim &&
            (values.tenPhim === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Trailer">
          <Input
          placeholder="Nhp trailer phim"
            name="trailer"
            onChange={formik.handleChange}
            value={formik.values.trailer}
          />
          {errors.trailer && touched.trailer && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>

        <Form.Item label="M t">
          <Input
            name="moTa"placeholder="Nhp m t"
            onChange={formik.handleChange}
            value={formik.values.moTa}
          />{" "}
          {errors.moTa && touched.moTa && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>

        <Form.Item label="Ngy khi chiu">
          <DatePicker
          placeholder="Ngy khi chiu"
            name="ngayKhoiChieu"
            format={"DD-MM-YYYY"}
            onChange={handleChangeDatapicker}
            value={moment(formik.values.ngayKhoiChieu)}
          />{" "}
          {errors.ngayKhoiChieu && touched.ngayKhoiChieu && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
        </Form.Item>
        <Form.Item label="nh gi">
          <Input
          placeholder="nh gi"
            style={{ width: "100px" }}
            name="danhGia"
            onChange={formik.handleChange}
            value={formik.values.danhGia}
          />
          {errors.danhGia &&
            touched.danhGia &&
            (values.danhGia === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Ch c nhp s (1-10) !</p>
              </div>
            ))}
        </Form.Item>

        <Form.Item label="Hnh nh">
          <Input
            disabled
            type="file"
            name="hinhAnh"
            onChange={handleChangeFile}
            accept="image/jpg,image/jpeg,image/gif,image/png"
            value={formik.values.hinhAnh}
          />{" "}
          {errors.hinhAnh && touched.hinhAnh && (
            <div className="styleErrors">
              <p>_ Khng c  trng</p>
            </div>
          )}
          <br />
          <img
            style={{ width: "150px", height: "150px" }}
            alt="..."
            src={imgSrc === "" ? editMovieDetail?.hinhAnh : imgSrc}
          />
        </Form.Item>
        <Form.Item className="text text-center btnEditMovie">
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button "
          >
            Cp nht
          </Button>
          <Button
            type="primary"
            onClick={() => {
              history.push("/admin/movie");
            }}
            className="login-form-button ml-5"
          >
            Quay li
          </Button>
        </Form.Item>
      </Form>
    </>
  );
}
