import React from "react";
import { Form, Input, Button, Select } from "antd";
import { useFormik } from "formik";
import { GROUP_ID } from "settings/apiConfig";
import userApi from "apis/userApi";
import { useDispatch, useSelector } from "react-redux";
import { actGetAllUser } from "./module/action";
import { NavLink } from "react-router-dom";
import * as yup from "yup";
import "./User.scss";

//CONTENT
export default function AddUser() {
  const dispatch = useDispatch();
  const { currentUser } = useSelector((state) => state.authUserReducer);
  //to form  lu tr thng tin nhp t input
  const formik = useFormik({
    initialValues: {
      taiKhoan: "",
      matKhau: "",
      email: "",
      soDt: "",
      maNhom: GROUP_ID,
      maLoaiNguoiDung: "",
      hoTen: "",
    },
    validationSchema: yup.object({
      taiKhoan: yup.string().matches(/^\w+$/).required(),
      matKhau: yup
        .string()
        .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{6,10}$/)
        .required(),
      email: yup.string().email().required("Email khng ng nh dng"),
      soDt: yup
        .string()
        .matches(/^[0-9]*$/)
        .max(10)
        .required("S in thoi cha ng nh dng !"),
      maNhom: yup.string(),
      maLoaiNguoiDung: yup.string().required("Bn cha chn loi ngi dng !"),
      hoTen: yup
        .string()
        .matches(
          /^[a-zA-Z ,.'-]+$/
        )
        .required(),
    }),
    onSubmit: (values) => {
      userApi
        .addUser(values, currentUser.accessToken)
        .then((res) => {
          alert("Thm thnh cng !");
          dispatch(actGetAllUser());
        })
        .catch((err) => {
          console.log(err.response?.data);
        });
    },
  });
  const handleChangeLoaiNguoiDung = (value) => {
    formik.setFieldValue("maLoaiNguoiDung", value);
  };
  // to bin ring  trnh s dng li nhiu nh hng performance
  const errors = formik.errors;
  const touched = formik.touched;
  const values = formik.values;
  return (
    <>
      <h2>THM NGI DNG </h2>
      <Form
        className="text-left"
        onSubmitCapture={formik.handleSubmit}
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 10,
        }}
      >
        <Form.Item label='Ti khon'>
          <Input
            name="taiKhoan"
            placeholder="Nhp ti khon"
            onChange={formik.handleChange}
          />
          {errors.taiKhoan &&
            touched.taiKhoan &&
            (values.taiKhoan === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng du cu v cc k t c bit</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Mt khu">
          <Input name="matKhau" 
            placeholder="Nhp mt khu"
            onChange={formik.handleChange} />
          {errors.matKhau &&
            touched.matKhau &&
            (values.matKhau.length < 6 || values.matKhau.length > 10 ? (
              <div className="styleErrors">
                <p>_  di k t t 6 n 10</p>
                <p>_ Khng s dng k t c bit</p>
                <p>_ t nht 1 k t thng , hoa v ch s</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng s dng k t c bit</p>
                <p>_ t nht 1 k t thng , hoa v ch s</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="H tn">
          <Input name="hoTen"
            placeholder="Nhp h tn"
            onChange={formik.handleChange} />
          {errors.hoTen &&
            touched.hoTen &&
            (values.hoTen === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Khng cha cc k t c bit v du cu</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Khng cha cc k t c bit v du cu</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="S in thoi">
          <Input name="soDt" 
            placeholder="Nhp s in thoi"
            onChange={formik.handleChange} />
          {errors.soDt &&
            touched.soDt &&
            (values.soDt === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ S in thoi khng ng nh dng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ S in thoi khng ng nh dng</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Email">
          <Input name="email"
          
          placeholder="Nhp email"
          onChange={formik.handleChange} />
          {errors.email &&
            touched.email &&
            (values.email === "" ? (
              <div className="styleErrors">
                <p>_ Khng c  trng</p>
                <p>_ Email khng ng nh dng</p>
              </div>
            ) : (
              <div className="styleErrors">
                <p>_ Email khng ng nh dng</p>
              </div>
            ))}
        </Form.Item>
        <Form.Item label="Loi ngi dng">
          <Select
            name="maLoaiNguoiDung"
            options={[
              { label: "Qun Tr", value: "QuanTri" },
              { label: "Khch hng", value: "KhachHang" },
            ]}
            onChange={handleChangeLoaiNguoiDung}
            placeholder="Chn loi ngi dng"
          />
          {formik.errors.maLoaiNguoiDung && formik.touched.maLoaiNguoiDung && (
            <div className="styleErrors">
              <p>{formik.errors.maLoaiNguoiDung}</p>
            </div>
          )}
        </Form.Item>

        <Form.Item className="btnAddUser" style={{ marginLeft: "295px" }}>
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button btnAdd"
          >
            Thm
          </Button>
          <NavLink to="/admin/user">
            <Button type="primary" className="login-form-button btnBack ml-5">
              Quay li
            </Button>
          </NavLink>
        </Form.Item>
      </Form>
    </>
  );
}
